  color: #353535;
  margin: 0 !important;
  transition: 0.3s;
  font-weight: 400;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.collapsible-body {
  overflow: hidden;
}

.footer {
  margin-top: auto;
}

.footer-out {
  height: 60px;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box !important;
  outline: none;
}

.number-input-button {
  border: none;
  border: 2px solid #000;
  width: 20px;
  height: 20px;
  background-color: #fff;
  font-weight: bold;
  color: green;
}

.number-result {
  width: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.number-input-container {
  display: flex;
}

.number-input {
  box-shadow: inset 0 0 0 rgba(64, 150, 255, 0.1);
  margin: 1rem 0;
  pointer-events: none;
  text-align: center;
  border: none;
  width: 30px;
  display: none;
  text-align: center;
}

#scene {
  position: relative;
  width: 100%;
}

.wrapper {
  max-width: 1280px;
  width: 100%;
  margin: 0 auto;
  display: flex;
  height: 100%;
}

@media screen and (max-width: 1430px) {
  .wrapper {
    max-width: 1100px;
  }
}

@media screen and (max-width: 1279px) {
  .wrapper {
    max-width: 900px;
  }
}

@media screen and (max-width: 1023px) {
  .wrapper {
    max-width: 700px;
  }
}

@media screen and (max-width: 768px) {
  .wrapper {
    max-width: 580px;
    padding: 0 15px;
  }
}

svg {
  fill: transparent;
}

.swiper-container {
  overflow: visible !important;
}

.m-bg-cont {
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
}

.m-bg-cont img {
  display: none;
}

.m-select {
  background: #FFFFFF;
  border: 1px solid #E2E2E2 !important;
  height: 50px !important;
  border-radius: 0 !important;
  max-width: 270px;
  width: 100%;
  height: 50px;
  padding-top: 4px;
  display: flex;
  align-items: center;
  font-style: normal !important;
  font-weight: 300 !important;
  font-size: 16px !important;
  color: #323232 !important;
}

.m-select:after {
  border-color: #2B7EE0 !important;
}

.m-select .list {
  width: calc(100% + 2px) !important;
  left: -1px !important;
  max-height: 270px;
  overflow-y: auto;
  border-top-left-radius: 0 !important;
  border-top-right-radius: 0 !important;
}

.m-select .list .option {
  height: 45px;
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 128%;
  color: #323232;
  display: flex;
  align-items: center;
  border-bottom: 1px solid #D7DEE9 !important;
}

.m-select .list .option:hover {
  background-color: #F6F9FE;
}

.m-select .list .option:last-child {
  border-bottom: 0;
}

.m-select .list .option.focus {
  background-color: #F6F9FE !important;
  font-weight: 300 !important;
}

.m-select.open {
  border-color: #2B7EE0 !important;
  border-bottom: none !important;
}

.m-select.open .list {
  min-width: 100% !important;
  transform: translateY(-4px) !important;
  z-index: 12 !important;
  border-left: 1px solid #2B7EE0 !important;
  border-right: 1px solid #2B7EE0 !important;
  box-sizing: content-box;
  border-bottom: 1px solid #2B7EE0 !important;
}

#slider {
  width: 100%;
}

.noUi-base {
  background-color: #e2e2e2;
}

.noUi-horizontal .noUi-handle,
.noUi-vertical .noUi-handle {
  top: -4.5px;
}

.noUi-target.noUi-horizontal .noUi-tooltip {
  background: #2062B0;
}

.noUi-connect {
  background: #2062B0;
}

.noUi-horizontal .noUi-handle {
  background: #2062B0;
  width: 10px;
  height: 11px;
  border-radius: 3px !important;
  background-color: #2062B0 !important;
}

.noUi-target {
  background: #2062B0;
}

.noUi-active {
  border-radius: 40px 40px 40px 40px !important;
}

a {
  text-decoration: none;
  color: #323232;
  font-weight: 400;
}

.noUi-tooltip span {
  display: none;
}

.noUi-tooltip {
  width: 100px;
  transform: rotate(0) translatex(-50%) !important;
  background-color: transparent !important;
  opacity: 1 !important;
  color: black !important;
}

.noUi-tooltip span {
  transform: none !important;
  color: black !important;
  opacity: 1 !important;
  top: calc(100% + 23px) !important;
  font-style: normal !important;
  font-weight: 500 !important;
  font-size: 12px !important;
  line-height: 15px !important;
  text-align: center !important;
  color: #4F4F4F !important;
}

.collapsible-body {
  transition: 0.2s;
  overflow: hidden;
}

.ymap {
  height: 100%;
  width: 60vw;
  position: absolute;
  top: 0;
  left: 0;
}

.header__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.header__drop {
  position: absolute;
  top: 100%;
  right: 0;
  width: 622px;
  flex-direction: column;
  display: flex;
  z-index: 10;
  background: #FFFFFF;
  box-shadow: 0px 6px 12px rgba(0, 0, 0, 0.16), 0px 2px 4px rgba(0, 0, 0, 0.04);
  border-radius: 8px;
  padding: 30px;
  opacity: 0;
  visibility: hidden;
  transition: 0.2s;
}

.header__drop .m-button-2 {
  margin-top: 25px;
}

.header__drop-img {
  height: 64px;
  width: 53px;
  margin-right: 18px;
}

.header__drop-items {
  width: 100%;
}

.header__drop-content {
  margin-right: auto;
}

.header__drop-item {
  display: flex;
  width: 100%;
  justify-content: space-between;
  margin-bottom: 16px;
  padding-bottom: 16px;
  border-bottom: 1px solid #F1F4F8;
}

.header__drop-item:last-child {
  margin-bottom: 0;
  padding-bottom: 0;
  border-bottom: none;
}

.header__drop-article {
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 140%;
  color: #9CA4B1;
  margin-bottom: 8px;
}

.header__drop-article span {
  color: #333132;
}

.header__basket {
  position: relative;
}

.header__basket:hover .header__drop {
  opacity: 1;
  visibility: visible;
}

.header__top {
  height: 48px;
  align-items: center;
  background-color: #F1F4F8;
}

.header__logo {
  cursor: pointer;
}

.header__logo:active {
  transform: translatey(1px);
}

@media screen and (max-width: 425px) {
  .header__logo img {
    height: 40px;
  }
}

.header__address {
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 112%;
  border-bottom: 1px dashed #1A6BF2;
  padding-bottom: 2px;
  color: #1A6BF2;
  margin-right: 30px;
  transition: 0.2s;
}

.header__address:hover {
  border-color: transparent;
}

@media screen and (max-width: 425px) {
