}

@media screen and (max-width: 1023px) {
  .header__catalog {
    display: none;
  }
}

.header__menu {
  display: flex;
  margin-right: auto;
}

@media screen and (max-width: 1279px) {
  .header__menu {
    display: none;
  }
}

.header__menu a {
  margin: 0 20px;
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 112%;
  color: #63686F;
  transition: 0.2s;
  display: flex;
  align-items: center;
  border-bottom: 1.5px dashed transparent;
}

.header__menu a:hover {
  border-color: #1A6BF2;
