  }
}

.header__mobile {
  display: none;
  align-items: center;
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 112%;
  height: 40px;
  padding: 0 10px;
  color: #333132;
}

.header__mobile svg {
  margin-right: 15px;
}

@media screen and (max-width: 1279px) {
  .header__mobile {
    display: flex;
  }
}

.header__mobile-menu {
  display: none;
}

.header__mobile-menu svg {
  margin-bottom: 15px;
}

@media screen and (max-width: 1279px) {
  .header__mobile-menu {
    display: flex;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    max-height: 100vh;
    z-index: 30;
    flex-direction: column;
    width: 250px;
    background-color: #fff;
    padding: 20px;
    transform: translate(100%);
    transition: 0.2s;
    opacity: 0;
    overflow-y: auto;
  }
  .header__mobile-menu.active {
    opacity: 1;
    visibility: visible;
    transform: none;
  }
  .header__mobile-menu a {
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    line-height: 112%;
    color: #63686F;
    transition: 0.2s;
    display: flex;
    align-items: center;
    padding: 10px 0;
    border-bottom: 1.5px dashed transparent;
  }
  .header__mobile-menu a:hover {
    border-color: #1A6BF2;
    color: #1A6BF2;
  }
}

.header__mobile-phone {
  font-size: 20px !important;
  color: #FF5927 !important;
}

.header__bottom-right {
  display: flex;
  align-items: center;
}

@media screen and (max-width: 1023px) {
