.m-check {
  cursor: pointer;
  display: flex;
  margin-bottom: 16px;
  align-items: center;
}

.m-check input {
  display: none;
}

.m-check input:checked ~ div svg {
  opacity: 1;
}

.m-check div {
  max-width: 20px;
  min-width: 20px;
  height: 20px;
  border: 1.5px solid #D9DDE4;
  box-sizing: border-box;
  border-radius: 4px;
  position: relative;
  margin-right: 12px;
}

.m-check div:hover {
  border-color: #9CA4B1;
}

.m-check div svg {
  position: absolute;
  top: 50%;
  left: 50%;
  opacity: 0;
  transition: 0.2s;
  transform: translate(-50%, -50%);
}

.m-check p {
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 112%;
  color: #63686F;
}

