.pageNavFixed {
  position: fixed;
  z-index: 1500;
  top: 0;
  left: 0;
  left: -460px;
  width: 460px;
  padding: 30px;
  box-sizing: border-box;
  border-radius: 0px 0px 30px 0px;
  background-color: #fff;
  transition: 0.5s;
  border-right: 2px solid black;
  border-bottom: 2px solid black;
}

.pageNavFixed:hover {
  left: 0px;
}

.pageNavFixed::after {
  content: 'NAV';
  position: absolute;
  width: 50px;
  height: 30px;
  top: 0;
  right: -50px;
  border-radius: 0px 0px 5px 0px;
  background-color: black;
  color: #7EB325;
  padding: 5px;
  padding-left: 10px;
  box-sizing: border-box;
  cursor: pointer;
}

.pageNavFixed__cont {
  width: 100%;
  max-height: 600px;
  overflow-x: hidden;
  overflow-y: auto;
}

.pageNavFixed__cont a {
  display: block;
  font-size: 16px;
  line-height: 20px;
  color: #7EB325;
  margin-bottom: 10px;
}

.pageNavFixed__cont a:hover {
  text-decoration: underline;
}

.pageNavFixed__cont a.completed {
  color: green;
}

.pageNavFixed__cont b {
  display: block;
  margin-bottom: 15px;
}

.pageNavFixed__cont div {
  margin: 0;
  margin-bottom: 50px;
}

.blur {
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  position: fixed;
  background: rgba(0, 0, 0, 0.24);
  z-index: 29;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  padding: 0 20px;
  opacity: 0;
  visibility: hidden;
  transition: 0.3s;
  overflow-y: scroll;
}

.blur > {
  cursor: pointer !important;
}

.blur.active {
  opacity: 1;
  visibility: visible;
}

.popup {
  opacity: 0;
  transition: 0.2s;
  visibility: hidden;
  display: none;
}

.popup.active {
  display: flex;
  flex-direction: column;
  opacity: 1;
  visibility: visible;
}

.modal-close {
  cursor: pointer;
}

.contact__bottom {
  width: 100%;
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
}

.upload {
  display: flex;
  justify-content: flex-start;
}

@media screen and (max-width: 767px) {
  .upload {
    margin-top: 15px;
  }
}

.upload label {
  display: flex;
  width: 100%;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 112%;
  color: #63686F;
  cursor: pointer;
  display: flex;
  align-items: center;
}

.upload label:hover {
  opacity: 0.8;
}

.upload label span {
  max-width: 200px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  display: block;
}

.upload input {
  border: 1px solid #ddd;
}

.upload svg {
  margin-right: 10px;
}

.upload .inputfile {
  display: none;
}

.modal {
  position: relative;
}

.modal__close {
  position: absolute;
  top: 40px;
  right: 40px;
  cursor: pointer;
}

.modal__close svg {
  transition: 0.2s;
}

.modal__close:hover svg {
  transform: rotate(90deg);
}

.modal__title {
  font-style: normal;
  font-weight: bold;
  font-size: 32px;
  line-height: 112%;
  color: #333132;
  margin-bottom: 25px;
  padding: 0 20px;
}

.modal__table {
  max-width: 100%;
  overflow-x: auto;
}

.modal__table table {
  width: 100%;
}

.modal__table th {
  padding-bottom: 10px;
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  line-height: 112%;
  color: #333132;
  text-align: left;
  padding-bottom: 10px;
  background-color: #fff;
  padding-right: 10px;
}

.modal__table th:first-child {
  padding-left: 20px;
}

.modal__table tr:nth-child(even) {
  background-color: #F6F6F6;
}

.modal__table td {
  padding: 18px 0;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 112%;
  color: #63686F;
  text-align: left;
  padding-right: 10px;
}

.modal__table td:first-child {
  padding-left: 20px;
}

.modal__city {
  position: relative;
}

.modal__city:before {
  content: '';
  height: 96px;
  background: linear-gradient(180deg, #FFFFFF 0%, rgba(255, 255, 255, 0) 100%);
  transform: rotate(-180deg);
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 1;
  pointer-events: none;
  position: absolute;
}

.modal__city-content {
  margin-top: 25px;
  position: relative;
  width: 100%;
  height: 260px;
  overflow-y: auto;
  padding-bottom: 96px;
}

.modal__city-item {
  display: flex;
}

.modal__city-item-left {
  margin-right: 16px;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 132%;
  color: #475AFF;
}

.modal__city-item-right {
  display: flex;
  flex-direction: column;
}

.modal__city-item-right span {
  margin-bottom: 10px;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 132%;
  color: #63686F;
  display: block;
}

.modal-1 {
  margin: 0 50px;
  max-width: 1060px;
  width: calc(100% - 100px);
  padding: 40px 20px;
  background: #FFFFFF;
  border-radius: 2px;
